$watch-size: 400px;
$stop-watch-size: 0.1 * $watch-size;
$sec-clockwise-size: 0.5 * $watch-size;
$min-clockwise-size: 0.7 * $sec-clockwise-size;
$sec-clockwise-width: 10px;
$min-clockwise-width: 1.5 * $sec-clockwise-width;
$color-main: #006400;
$color-second: #080;
$color-min-clockwise: #7cfc00;
$color-sec-clockwise: #0f0;
